import { observable, action, computed, makeObservable } from "mobx";
import { OPTION, VERSION_V2_OPEN, VERSION_V2, VERSION_V2_LITE, CALL_OPTION, PUT_OPTION, VERSION_V3, VERSION_V3_LITE, FUTURES, FUTURE } from '../utils/Constants'
import apiProxy from './ApiProxy'
import { isCakeLP, isSushiLP, getMarkpriceSymbol } from '../utils/utils'
import {POWER} from '../utils/Constants'
import { bg } from '../lib/web3js/index'
import { sortOptionSymbols } from "../lib/web3js/index";
import ApiProxy from "./ApiProxy";

const columnMap = {
  pool: 'address'
}
export default class Pool {
  name  = ''
  chainId = ''
  chain = ''
  zone = ''
  address = ''
  version = ''
  type = '' 
  isLp = ''
  bTokenSymbol = ''
  retired = ''
  rewarded=''
  new=''
  symbols = []
  bTokens = []
  decimals = 2
  markpriceSymbolFormat = ''
  currentSymbol = null

  constructor(data) {
    makeObservable(this, {
      chainId: observable,
      chain: observable,
      zone: observable,
      address: observable,
      retired: observable,
      symbols: observable,
      bTokens: observable,
      bTokenSymbol: observable,
      isLp: observable,
      decimals: observable,
      markpriceSymbolFormat: observable,
      currentSymbol: observable,
      setCurrentSymbol: action,
      setMarkpriceSymbolFormat: action,
      setAddress: action,
      setChain: action,
      setChainId: action,
      setZone: action,
      setVersion: action,
      setType: action,
      setSymbols: action,
      setBTokens: action,
      setDecimals: action,
      selectedSymbol: computed,
      isFuture: computed,
      isOption: computed,
      isInno: computed,
      isOpen: computed,
      isV3: computed,
      isV2 : computed,
      isV3Lite: computed,
      isAllV3: computed,
      isPower: computed
    })
    this.init(data)
  }

  init(data = {}) {
    const keys = Object.keys(data);
    for (const k in keys) {
      const name = columnMap[keys[k]] ? columnMap[keys[k]] : keys[k];
      if (this.hasOwnProperty(name) && data[keys[k]]) {
        const attr = name.replace(/^\S/, s => s.toUpperCase())
        const method = `set${attr}`
        if (this[method]) {
          this[method].call(this, data[keys[k]])
        } else {
          this[name] = data[keys[k]]
        }
      }
    }
  }

  async getVenusEarned(wallet,address){
    let res = await apiProxy.request('getVenusEarned',[wallet.detail.chainId,address,wallet.detail.account])
    return res
  }

  async getPoolInfo(wallet, pool) {
    let params = [pool.chainId, pool.address]
    let apyInfo = await apiProxy.request('getPoolInfoApy', params);
    let liq = await apiProxy.request('getPoolLiquidity', params);
    const detail = {
      liquidity: liq.totalValue || liq.totalLiquidity,
      apy: ((+apyInfo.maxApy) * 100).toFixed(2),
      bTokensApy: apyInfo.bTokens,
      bTokensLiq: liq.bTokens,
    }
    if (pool.isLp) {
      let lpApy = await apiProxy.request('getLpPoolInfoApy', params)
      const apy = ((+lpApy.apy2) * 100).toFixed(2);
      detail['lpApy'] = apy
      detail['isCakeLP'] = isCakeLP(pool.address)
      detail['isSushiLP'] = isSushiLP(pool.address)
    }
    return detail
  }

  async getIsStaked(wallet, pool) {
    let params = [pool.chainId, pool.address, wallet.detail.account]
    let bool = await apiProxy.request('getUserStakeInfo', params)
    return bool
  }

  async getLiquidityInfo(wallet, address, version, isLp, type, nowToken, bTokenId) {
    let params;
    if (version === 'v3' || version === 'v3_lite') {
      if(!nowToken){
        return false;
      }
      params = [wallet.detail.chainId, address, wallet.detail.account, nowToken]
    } else {
      params = [wallet.detail.chainId, address, wallet.detail.account, bTokenId]
    }
    let info = null;
    let res;
    if (isLp) {
      res = await apiProxy.request('getLpLiquidityInfo', params)
    } else {
      res = await apiProxy.request('getLiquidityInfo', params)
    }
    const shares = res.shares ? bg(res.shares) : bg(0)
    if (['clp', 'slp', 'v1', 'v2_lite', 'v2_lite_open'].includes(version) || (version === 'option' && type === 'perpetual') || (version === 'v2' && type === 'option')) {
      const total = shares.isNaN() ? bg(0) : shares.multipliedBy(res.shareValue)
      info = {
        total: res.poolLiquidity,
        shareValue: res.shareValue,
        percent: res.poolLiquidity > 0 ? total.dividedBy(res.poolLiquidity).multipliedBy(100).toString() : 0,
        shares: shares.toFixed(2),
        formatShares: shares.toFixed(2),
        totalShares: bg(shares).toString(),
        values: total.toFixed(4),
      }
    } else if (version === 'v2') {
      info = {
        total: res.poolLiquidity,
        pnl: (+res.pnl).toFixed(2),
        swappedPnl: (+res.swappedPnl).toFixed(2),
        pnlPercentage:((+res.pnlPercentage)*100).toFixed(2),
        percent: res.poolLiquidity > 0 ? shares.dividedBy(res.poolLiquidity).multipliedBy(100).toString() : 0,
        shares: shares.toFixed(2),
        formatShares: shares.toFixed(2),
        totalShares: bg(shares).toString(),
      }
    }else if (version === 'v3' || version === 'v3_lite') {
      info = {
        total: res.poolLiquidity,
        pnl: (+res.pnl).toFixed(6),
        swappedPnl: (+res.swappedPnl).toFixed(6),
        pnlPercentage:((+res.pnlPercentage)*100).toFixed(2),
        percent: res.poolLiquidity > 0 ? shares.multipliedBy(res.bTokenPrice).dividedBy(res.poolLiquidity).multipliedBy(100).toString() : 0,
        shares: shares.toFixed(6),
        formatShares: shares.toFixed(6),
        totalShares: bg(shares).toString(),
      }
    }
    return info
  }

  setChainId(chainId) {
    this.chainId = chainId;
  }

  setChain(chain) {
    this.chain = chain
  }

  setZone(zone) {
    this.zone = zone;
  }

  setAddress(address) {
    this.address = address;
  }

  setType(type) {
    this.type = type
  }

  setBTokenSymbol(bTokenSymbol){
    this.bTokenSymbol = bTokenSymbol;
  }

  async getVolume24Changed(){
    const params = [this.chainId,[this.address]]
    const symbols = await ApiProxy.request('getPoolMarkPrices24hInfo', params)
    return symbols.length > 0 ? symbols[0].volumn24h : ''
  }

  setSymbols(symbols) {
    symbols = symbols.map(s => {
      const tokenInfo = this.bTokens.length > 0 ? this.bTokens[0] : {}
      s.name = this.name;
      s.type = this.type
      s.address = this.address
      s.version = this.version
      s.bTokenSymbol = tokenInfo.bTokenSymbol
      s.chain = this.chain
      s.chainId = this.chainId
      if(/bsctestnet/.test(this.chain)) {
        s.chain = 'bsc'
      } else if(/arbitrum/.test(this.chain)) {
        s.chain = 'arbi'
      }
      s.zone = this.zone
      s.optionType = ''
      s.isPower = s.category === POWER
      s.isFuture = s.category === FUTURES
      s.isOption = s.category === OPTION
      s.isAllV3 = s.version === VERSION_V3 || s.version === VERSION_V3_LITE
      s.isV2 = s.version === VERSION_V2  || s.version === VERSION_V2_LITE
      s.getVolume24Changed =  this.getVolume24Changed
      // s.symbol = s.displaySymbol || s.symbol
      s.displaySymbol = s.displaySymbol || s.symbol,
      this.isV2 && this.setBTokenSymbol(tokenInfo.bTokenSymbol)
      s.formatedSymbol = (this.isOpen || this.isV2) && s.isFuture ? `${s.symbol}/${tokenInfo.bTokenSymbol}` : s.displaySymbol || s.symbol
      // s.unit = s.displayUnit ? s.displayUnit :  s.unit
      if(s.isPower){
        s.symbolWithoutPower = s.symbol.replace('^2','')
      }
      if (s.type.includes(OPTION)) {
        s.optionType = /c$/i.test(s.symbol) ? CALL_OPTION : PUT_OPTION
        const symbol = s.symbol.split('-')[0]
        s.underlier = symbol
        // s.zone = symbol
      }
      s.displayCategory = /s$/.test(s.category) ? s.category.toUpperCase() : `${s.category}s`.toUpperCase()
      if (!this.isOpen) {
        s.markpriceSymbolFormat = getMarkpriceSymbol(s)
      }
      s.getDecimalBySymbol = symbol => {
        const _symbol = symbols.find(s => s.symbol === symbol)
        return _symbol && +(_symbol.decimals || 2)
      }
      return s;
    })
    if (this.type.includes(OPTION)) {
      this.symbols = sortOptionSymbols(symbols)
    } else {
      this.symbols = symbols.sort((s1,s2) => {
        if(s1.rank > s2.rank){
          return 1
        } else if(s1.rank < s2.rank){
          return -1
        } else {
          return 0;
        }
      });
    }
  }


  setCurrentSymbol(symbol) {
    this.currentSymbol = symbol
  }


  setMarkpriceSymbolFormat(markpriceSymbolFormat) {
    this.markpriceSymbolFormat = markpriceSymbolFormat
  }

  setDecimals(decimals) {
    this.decimals = decimals
  }


  setVersion(version) {
    this.version = version
  }

  setBTokens(bTokens) {
    this.bTokens = bTokens;
  }

  setName(name){
    this.name = name;
  }

  get selectedSymbol() {
    if (this.currentSymbol) {
      return this.currentSymbol
    } else {
      return this.symbols.length > 0 ? this.symbols[0] : {}
    }
  }

  get isFuture() {
    return this.type.includes(FUTURE);
  }

  get isOption() {
    return this.type.includes(OPTION)
  }

  get isInno() {
    return this.version === VERSION_V2_LITE|| this.version === VERSION_V3_LITE
  }

  get isOpen() {
    return this.version === VERSION_V2_OPEN 
  }

  get isPower(){
    return this.category === POWER
  }

  get isV3() {
    return this.version === VERSION_V3
  }

  get isV2(){
    return this.version === VERSION_V2 || this.version === VERSION_V2_LITE
  }

  get isV3Lite() {
    return this.version === VERSION_V3_LITE
  }

  get isAllV3() {
    return this.isV3 || this.isV3Lite
  }


}