export const getSpecification = async (chainId, poolAddress, symbolId) => {
  const versionId = getPoolVersionId(poolAddress);
  if (versionId.failed) {
    return { success: false, response: versionId.response };
  }
  return api(versionId).getSpecification(chainId, poolAddress, symbolId);
};

