export const getPositionInfos = async(chainId, poolAddress, accountAddress) => {
  const versionId = getPoolVersionId(poolAddress);
  if (versionId.failed) {
    return { success: false, response: versionId.response };
  }
  const res  = await api(versionId).getPositionInfos(chainId, poolAddress, accountAddress);

  // format minValue of unrealizedPnl and fundingFee :(
  if (res.success && Array.isArray(res.response.data)) {
    const data = res.response.data
    const minValue = '0.000001'
    return data.map((p) => {
      if (p.unrealizedPnl && bg(p.unrealizedPnl).abs().lt(minValue)) {
        p.unrealizedPnl = '0'
      }
      if (p.fundingFee && bg(p.fundingFee).abs().lt(minValue)) {
        p.fundingFee = '0'
      }
      return p
    })
  } else {
    return res
  }
}

