export const isUnlocked = async (
  chainId,
  poolAddress,
  accountAddress,
  bTokenId
) => {
  const versionId = getPoolVersionId(poolAddress)
  if (versionId.failed) {
    // for deri
    return isDeriUnlocked(chainId, poolAddress);
  }
  return api(versionId).isUnlocked(chainId, poolAddress, accountAddress, bTokenId)
};

export const getEstimatedLiquidatePrice = async (
  chainId,
  poolAddress,
  accountAddress,
  newVolume,
  symbolId
) => {
  const versionId = getPoolVersionId(poolAddress)
  if (versionId.failed) {
    return { success: false, response: versionId.response }
  }
  if (versionId === 'v3') {
    return api(versionId).getEstimatedLiquidatePrice(chainId, poolAddress, accountAddress, newVolume)
  }
  if (LITE_VERSION_IDS.includes(versionId)) {
    return getEstimatedLiquidatePriceV2(chainId, poolAddress, accountAddress, newVolume, symbolId);
  } else if (versionId === 'option') {
    // place holder
    return { success: true, response: { data: '' } };
  }
  if (symbolId === undefined) {
    // place holder
    return { success: true, response: { data: '' } };
  } else {
    return getEstimatedLiquidatePriceV2(chainId, poolAddress, accountAddress, newVolume, symbolId);
  }
};

