import React, { useState, useEffect } from 'react'
import {DeriEnv } from "../../lib/web3js/index";
import styled from 'styled-components';
import { inject, observer } from 'mobx-react';
import {useWallet} from 'use-wallet'
import { useRouteMatch } from 'react-router';
import { formatAddress } from '../../utils/utils';
import Button from '../Button/Button';
import Icon from '../Icon/Icon';
import { show } from 'react-functional-modal';
import WalletDialog from '../WalletModal/WalletModal';
import AccountDialog from '../AccountModal/AccountModal';
import {useAlert} from 'react-alert'
import useConfig from '../../hooks/useConfig';
import { SUPPORTED_WALLETS } from '../../utils/Constants';
import { useCallback } from 'react';
import { useModal } from 'react-hooks-use-modal';
import walletViewBg from '../../assets/img/wallet-btn-bg.png'
import config from '../../page-network-config.json'
import {useLocation} from 'react-router-dom'
const Wrapper = styled.div`
  display : flex;
  align-items : center;
  color : #E0ECFF;
  cursor: pointer;
  img {
    margin-right : 8px;
  }
  .no-supported {
    white-space: nowrap;
  }
  .acc-wrapper {
    display : inherit;
  }
  &.wallet-view .acc-wrapper{
    background-color : rgba(85, 119, 253, 0.1);
    border: 1px solid #364F70;
    border-radius: 4px;
    // width: 175px;
    padding : 0 8px;
    height: 28px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &.wallet-view .acc-wrapper:hover {
    border : 1px solid #CD7A37;
    background-color : rgba(85, 119, 253, 0.1);
    background : url(${walletViewBg}) no-repeat;
  }
`


function WalletView({wallet,lang,isShowWalletModal}){
  const [btnText,setBtnText] = useState(lang['connect-wallet'])
  const alert = useAlert()
  const walletContext = useWallet();
  const [WalletModal, openWalletModal, closeWalletModal] = useModal('root', {
    preventScroll: true,
    closeOnOverlayClick: false
  });
  const [AccountModal, openAccountModal, closeAccountModal] = useModal('root', {
    preventScroll: true,
    closeOnOverlayClick: false
  });
  const location = useLocation();

  const showWalletModal = useCallback(() => {
    openWalletModal();
  },[walletContext,lang])

  const showAccountModal = useCallback(() => {
    openAccountModal();
  },[walletContext,lang])


  const setAccountText = (walletContext) => {
    //如果用户选择的网络正确
    if(walletContext.isConnected()){
      if(isWrongNetwork()) {
        setBtnText(<span className='no-supported'>{lang['unsupported-chain-id']}{walletContext.chainId}!</span>)
        wallet.setSupportCurNetwork(false)
      } else {
        setBtnText(<span className='address'>{formatAddress(walletContext.account)}</span>)
        wallet.setSupportCurNetwork(true)
      }
    } else {
      setBtnText(lang['connect-wallet'])
    } 
  }

  const isWrongNetwork = useCallback(() => {
    const chainList = config[DeriEnv.get()];
    const pathname = location.pathname;
    const page = Object.keys(chainList).find(key => pathname.match(new RegExp(`^${key}`)))
    const supportedChains = chainList[page]
    if(walletContext.isConnected()) {
      return supportedChains && !supportedChains.includes(walletContext.chainId)
    }
    return false
  },[walletContext])

  useEffect(() => {
    setAccountText(walletContext)
    if(walletContext.isConnected()){
      wallet.setDetail(walletContext)
      window.ethereum = walletContext.ethereum
    } else {
      wallet.setDetail({})
    }
  }, [walletContext,lang]);

  useEffect(() => {
    if(isShowWalletModal){
      showWalletModal()
    }
  }, [isShowWalletModal]);

  return (
    <Wrapper className='wallet-view'>
      {walletContext.isConnected() ? 
        <div onClick={showAccountModal} className='acc-wrapper'>
          <Icon token={SUPPORTED_WALLETS[walletContext.connector].icon} width='20'/>
          <div onClick={wallet.connect}>{btnText}</div>
        </div>
      :
        <Button label={btnText} className='m-connect' onClick={showWalletModal} width={184} height={32} fontWeight ={700} fontColor='#FFF' fontSize='18'/>
      }
      <WalletModal>
        <WalletDialog walletContext={walletContext} closeModal={closeWalletModal} lang={lang} wallet={wallet} isWrongNetwork={isWrongNetwork}/>
      </WalletModal>
      <AccountModal>
        <AccountDialog alert={alert} walletContext={walletContext} isWrongNetwork={isWrongNetwork} closeModal={closeAccountModal} blockExploreUrl={`${wallet.blockExploreUrl}/address/${walletContext.account}`} openWalletModal={openWalletModal} lang={lang}/>
      </AccountModal>
    </Wrapper>
  )
}
export default inject('wallet')(observer(WalletView))