// for Practice Earn activity

import { ADDRESS_ZERO, checkAddress, DeriEnv, Env } from "../../shared/config";
import { databasePEActivityFactory } from "../../shared/factory/database";
import { catchApiError, fromWei, bg } from "../../shared/utils";

const range = (n) => new Array(n).fill(0).map((i, index) => index);

const keyPrefix = (epoch) => {
  if (epoch && epoch.toString() === '1') {
    return DeriEnv.get() === 'prod' || DeriEnv.get() === 'testnet'
      ? 'PE1P1'
      : 'PE1T1';
  } else if (epoch && epoch.toString() === '2') {
    return DeriEnv.get() === 'prod' || DeriEnv.get() === 'testnet'
      ? 'PE2P1'
      : 'PE2T1';
  }
};

const claimKeyPrefix = (epoch) => {
  if (epoch && epoch.toString() === '1') {
    return DeriEnv.get() === 'prod' || DeriEnv.get() === 'testnet'
      ? 'PE1P1'
      : 'PE1T1';
  }
};

export const getEarnTop50Users = async (epoch) => {
  return catchApiError(async () => {
    //console.log('key', keyPrefix(epoch))
    const count = epoch.toString() === '2' ? 100 : 50;
    const perpertyCount = 2;
    const env = DeriEnv.get()
    const db = databasePEActivityFactory(env === Env.TESTNET || env === Env.PROD ? true : false);
    const key = range(count).reduce(
      (acc, i) =>
        acc.concat([
          `${keyPrefix(epoch)}.toppnl.${i + 1}.account`,
          `${keyPrefix(epoch)}.toppnl.${i + 1}.pnl`,
        ]),
      []
    );
    //console.log(key)
    const res = await db.getValues(key);
    let result = [];
    if (Array.isArray(res) && res.length === 2 * count) {
      for (let i = 0; i < res.length; i++) {
        if ((i + 1) % perpertyCount === 0) {
          const info = res.slice(i + 1 - perpertyCount, i + 1);
          const index = (i + 1) / perpertyCount;
          result.push({
            index,
            account: info[0].slice(0, 42),
            pnl: fromWei(info[1]),
          });
        }
      }
    }
    return result.filter((r) => r.account !== ADDRESS_ZERO);
  }, [], []);
};

export const getUserEarnInfo = async (accountAddress, epoch) => {
  return catchApiError(
    async () => {
      accountAddress = checkAddress(accountAddress);
      const env = DeriEnv.get()
      const db = databasePEActivityFactory(env === Env.TESTNET || env === Env.PROD ? true : false);
      const key = [
        `${keyPrefix(epoch)}.account.${accountAddress}.pnl`,
        `${keyPrefix(epoch)}.account.${accountAddress}.rank`,
      ];
      const res = await db.getValues(key);
      return {
        account: accountAddress,
        pnl: bg(res[0]).gt(bg(2).pow(255))
          ? bg(res[0]).minus(bg(2).pow(256)).div(bg(10).pow(18)).toString()
          : fromWei(res[0]),
        // rank: res[1],
      };
    },
    [],
    { account: accountAddress, pnl: '', rank: '' }
  );
};