import { getNetworkName } from "../utils"

export const getPoolName = (chainId, poolAddress) => {
  let poolType
  if (poolAddress === '0xc2EdaD668740f1aA35E4D8f227fB8E17dcA888Cd') {
    poolType = 'sushi'
  } else if (poolAddress === '0x73feaa1eE314F8c655E354234017bE2193C9E24E') {
    poolType = 'pancake'
  } else if (poolAddress === '0x26bE73Bdf8C113F3630e4B766cfE6F0670Aa09cF') {
    poolType = 'innoV2deri'
  }
  if (!poolType) {
    throw new Error(`claimReward: unsupported pool ${poolAddress}`)
  }
  return poolType
}

export const fixGetNetworkName = (chainId) => {
  let networkName = getNetworkName(chainId);
  return networkName.toLowerCase() === 'polygon' ? 'matic' : networkName;
};