import { checkChainId } from "../../shared/config";
import { catchApiError, catchTxApiError } from "../../shared/utils";
import { poolImplementationFactory } from "../../v3/contract/factory/pool";
import { ERC20Factory } from "../../v3/contract/factory/rest";
import { getSymbolsOracleInfo } from "../../v3/utils/oracle";
import { pTokenAirdropV3Factory } from "../contract/factory";

const getConfig = (chainId) => {
  const config = [
    {
      chainId: '56',
      address: '0xa7aC0329272b050B84333C0b0b1bc38F0c682a0a',
      pool: '0xD2D950e338478eF7FeB092F840920B3482FcaC40',
    }
  ]
  const res = config.find((c) => c.chainId === chainId)
  if (res) {
    return res
  } else {
    throw new Error(`cannot get pToken airdrop config with chainId(${chainId})`)
  }
}


export const isUserInPTokenWhiteListV3 = async (
  chainId,
  accountAddress
) => {
  chainId = checkChainId(chainId)
  return catchApiError(async () => {
    const config = getConfig(chainId);
    const pTokenAirdrop = pTokenAirdropV3Factory(chainId, config.address);
    return await pTokenAirdrop.whitelistAddresses(
      accountAddress
    );
  }, [], false);
};

export const isPTokenAirdroppedV3 = async (chainId, accountAddress) => {
  chainId = checkChainId(chainId)
  return catchApiError(async() => {
    const config = getConfig(chainId);
    const pTokenAirdrop = pTokenAirdropV3Factory(chainId, config.address);
    return await pTokenAirdrop.airdropedAccounts(
      accountAddress
    );
  }, [], false)
};

export const isUserPTokenExistV3 = async (chainId, accountAddress) => {
  chainId = checkChainId(chainId)
  return catchApiError(async () => {
    const config = getConfig(chainId);
    const pool = poolImplementationFactory(chainId, config.pool);
    await pool.init()
    const tokenId = await pool.pToken.getTokenIdOf(accountAddress);
    if (typeof tokenId === 'string' && parseInt(tokenId) > 0) {
      return true
    } else {
      return false
    }
    //return await pool.pToken.exists(tokenId)
  }, [], false);
};

export const isBTokenUnlockedV3 = async(chainId, accountAddress) => {
  chainId = checkChainId(chainId)
  return catchApiError(async() => {
    const config = getConfig(chainId);
    const pTokenAirdrop = pTokenAirdropV3Factory(chainId, config.address);
    const bTokenAddress = await pTokenAirdrop.airdropBTokenAddress();
    return await ERC20Factory(chainId, bTokenAddress).isUnlocked(accountAddress, config.pool)
  }, [], '')
}

export const unlockBTokenV3 = async (chainId, accountAddress, opts) => {
  chainId = checkChainId(chainId)
  return catchTxApiError(async () => {
    const config = getConfig(chainId);
    const pTokenAirdrop = pTokenAirdropV3Factory(chainId, config.address);
    const bTokenAddress = await pTokenAirdrop.airdropBTokenAddress();
    return await ERC20Factory(chainId, bTokenAddress).unlock(
      accountAddress,
      config.pool,
      opts,
    );
  }, [chainId, opts]);
};

export const airdropPTokenV3 = async (chainId, accountAddress, opts) => {
  chainId = checkChainId(chainId)
  return catchTxApiError(async () => {
    const config = getConfig(chainId);
    const pTokenAirdrop = pTokenAirdropV3Factory(chainId, config.address);
    const pool = poolImplementationFactory(chainId, config.pool);
    await pool.init()
    const oracleSignatures = await getSymbolsOracleInfo(chainId, pool.symbols.map((s) => s.symbol))
    return await pTokenAirdrop.airdropPToken(
      accountAddress,
      oracleSignatures,
      opts,
    );
  }, [chainId, opts]);
};
