import { claimReward as claimRewardV2Lite } from '../shared/api/mining_api'
import { claimReward as claimRewardV3 } from '../v3/api/transaction_api'

import { getPoolVersionId } from '../shared/config/pool';
import { api } from './api_shared'
import { getPoolConfigList } from './config_api';
import { DeriEnv, getLpConfigList } from '../shared/config';

export const addLiquidity = async (
  chainId,
  poolAddress,
  accountAddress,
  amount,
  bTokenId,
  opts,
) => {
  const versionId = getPoolVersionId(poolAddress)
  if (versionId.failed) {
    return { success: false, response: versionId.response }
  }
  // fix v2_lite bTokenId undefined issue
  if (bTokenId == null && versionId.startsWith('v2_lite')) {
    bTokenId = opts
  }
  return api(versionId).addLiquidity(
    chainId,
    poolAddress,
    accountAddress,
    amount,
    bTokenId,
    opts,
  );
}

export const removeLiquidity = async (
  chainId,
  poolAddress,
  accountAddress,
  amount,
  bTokenId,
  isMaximum,
  opts,
) => {
  if (opts == null && typeof isMaximum === 'object') {
    opts = isMaximum
    isMaximum = false
  }
  const versionId = getPoolVersionId(poolAddress)
  if (versionId.failed) {
    return { success: false, response: versionId.response }
  }
  // fix v2_lite bTokenId undefined issue
  if (bTokenId == null && versionId.startsWith('v2_lite')) {
    bTokenId = opts
  }
  return api(versionId).removeLiquidity(chainId, poolAddress, accountAddress, amount, bTokenId, isMaximum, opts)
}

export const claimReward = async (
  chainId,
  poolAddress,
  accountAddress,
  opts,
) => {
  let result = await getPoolConfigList(DeriEnv.get());
  let configs = [];
  if (result.success) {
    configs = result.response.data;
  }
  // add lp config
  configs = configs.concat(await getLpConfigList(DeriEnv.get()));
  const config = configs.find((v) => v.pool === poolAddress);
  let res
  if (config && ['v3', 'v3_lite', 'v2_lite', 'slp', 'clp'].includes(config.version)) {
    if (['v3', 'v3_lite'].includes(config.version)) {
      res = await claimRewardV3(chainId, poolAddress, accountAddress, opts)
    } else {
      res = await claimRewardV2Lite(chainId, poolAddress, accountAddress, opts)
    }
  } else {
    res = {
      success: false,
      response: {
        data: '',
        error: { message: 'The pool version is invalid and should in [v3, v2_lite, slp and clp]' },
      },
    };
  }
  return res
};
