import Header from './components/Header/Header';
import Footer from './components/Footer/Footer';
import Announcement from './components/Announcement/Announcement'
import Welcome from './components/Welcome/Welcome';
import Intro from './components/Intro/Intro'
import PageRouter from './pages/PageRouter';
import { useLocation } from 'react-router-dom'
import { DeriEnv } from './lib/web3js/index'
import './app.less';
import './assets/css/fonts.less'
import classNames from 'classnames';
import { show } from 'react-functional-modal'
import loadingImg from './assets/img/loading.png'
import React, { useEffect, useState } from 'react'
import { inject, observer } from 'mobx-react';
import { getCookie, setCookie } from './utils/utils';
import close from './assets/img/close-modal-icon.svg'
function App() {
  const env = DeriEnv.get()
  const location = useLocation();
  const curRouterClass = location.pathname.split('/')[1]
  const isPoolMessage = location.pathname.split('/')[5]
  const trade = location.pathname.split('/')[2]
  const dipHint = curRouterClass !== "governance" && curRouterClass !== "dipthree" && curRouterClass !== "dipone" && curRouterClass !== "diptwo" && curRouterClass !== "dipfour"
  const rootClass = classNames('deri-root', curRouterClass, trade)
  const [loadClass, setloadClass] = useState('')
  const [isShow, setIsShow] = useState(false)
  const closeMessage = () => {
    setCookie('close-pool-message', 'false')
    setIsShow(false)
  }
  useEffect(() => {
    setloadClass(classNames('loading', 'hide'))
    let cookie = getCookie('welcome')
    let cookiePool = getCookie('close-pool-message')
    if (cookiePool === 'false') {
      setIsShow(false)
    } else {
      setIsShow(true)
    }
    // if (cookie !== 'false' && (curRouterClass === 'pool' || curRouterClass === 'trade')) {
    //   show(<Welcome />, {
    //     key: 'Welcome',
    //     fading: true,
    //     style: {
    //       background: "rgba(0, 0, 0, 0.4)",
    //       zIndex: 2,
    //     }
    //   })
    // }

  }, [])
  return (
    <div className={`deri-root ${curRouterClass}  ${trade}`}>
      {env === 'testnet' && <Announcement />}
      {trade === 'powers' && <Intro />}
      {isPoolMessage === "0x4ad5cb09171275A4F4fbCf348837c63a91ffaB04" && isShow && <div className='message-pool-bsc'>
        Any user with deposit in the retired Future Main pool, please remove the liquidity and migrate to the Arbitrum Main Pool / BNB Chain Main Pool to stay earning DERI rewards.
        <a target='_blank' href="https://deri-protocol.medium.com/a-step-by-step-guide-for-liquidity-migration-a07b465ba510">Learn More</a>
        <img src={close} onClick={closeMessage} />
      </div>}

      <Header></Header>
      <PageRouter></PageRouter>
    </div>
  );
}

export default App
