import { getLpConfig } from '../config';
import { unlockSlp, addSlpLiquidity, removeSlpLiquidity } from './slp_pool_api';
// import { unlockClp, addClpLiquidity, removeClpLiquidity } from './clp_pool_api';
import {
  unlockClp2,
  addClp2Liquidity,
  removeClp2Liquidity,
} from './clp2_pool_api';
import { catchTxApiError } from '../utils';

export const unlockLp = async (chainId, poolAddress, accountAddress, opts) => {
  return catchTxApiError(async () => {
    const { version } = await getLpConfig(chainId, poolAddress);
    if (version === 'slp') {
      return await unlockSlp(chainId, poolAddress, accountAddress, opts);
      // } else if (version === 'clp') {
      //   return await unlockClp(chainId, poolAddress, accountAddress);
    } else if (version === 'clp') {
      return await unlockClp2(chainId, poolAddress, accountAddress, opts);
    }
  }, [chainId, opts])
};

export const addLpLiquidity = async (
  chainId,
  poolAddress,
  accountAddress,
  amount,
  opts
) => {
  return catchTxApiError(async () => {
    const { version } = await getLpConfig(chainId, poolAddress);
    if (version === 'slp') {
      return await addSlpLiquidity(chainId, poolAddress, accountAddress, amount, opts);
      // } else if (type === 'clp') {
      //   return await addClpLiquidity(chainId, poolAddress, accountAddress, amount);
    } else if (version === 'clp') {
      return await addClp2Liquidity(chainId, poolAddress, accountAddress, amount, opts);
    }
  }, [chainId, opts])
};

export const removeLpLiquidity = async (
  chainId,
  poolAddress,
  accountAddress,
  amount,
  opts,
) => {
  return catchTxApiError(async () => {
    const { version } = await getLpConfig(chainId, poolAddress);
    if (version === 'slp') {
      return await removeSlpLiquidity(
        chainId,
        poolAddress,
        accountAddress,
        amount,
        opts,
      );
    } else if (version === 'clp') {
      return await removeClp2Liquidity(
        chainId,
        poolAddress,
        accountAddress,
        amount,
        opts,
      );
    }
  }, [chainId, opts])
};
