import React, { useState, useEffect } from 'react'
import { setCookie, getCookie } from '../../utils/utils'
import classnames from 'classnames'
import logo from './img/logo.svg'
import close from '../../assets/img/close-modal-icon.svg'
import step from './img/intro-to-powers-logo.svg'
import step1 from './img/unique-payout.svg'
import step2 from './img/ola-mbtc-logo.svg'
import open from './img/open.svg'
import './intro.less'
export default function Intro() {
  const [index, setIndex] = useState(1)
  const [nowIndex, setNowIndex] = useState('one')
  const [show, setShow] = useState(false)
  const nextClick = () => {
    let i = index
    ++i
    if (i === 2) {
      setNowIndex('two')
    } else if (i === 3) {
      setNowIndex('three')
    } else {
      setNowIndex('one')
    }
    setIndex(i)
  }
  const setStep = (i) => {
    setIndex(i)
    if (i === 2) {
      setNowIndex('two')
    } else if (i === 3) {
      setNowIndex('three')
    } else {
      setNowIndex('one')
    }
  }
  const stepClass = classnames('step', nowIndex)
  const closeIntro = () => {
    setCookie('introShow', "false")
    setShow(false)
  }
  const openShow = () => {
    setIndex(1)
    setNowIndex('one')
    setShow(true)
  }
  useEffect(() => {
    let cookie = getCookie('introShow')
    if (cookie === 'false') {
      setShow(false)
    } else {
      // setShow(true)
    }

  }, [])

  return (
    <>
      {show && <div className='intro-box'>
        <div className='intro'>
          <div className='intro-header'>
            <div className='deri-logo'>
              <img src={logo} alt='DERI' />
            </div>
            <div className='close-logo' onClick={closeIntro}>
              <img src={close} alt='close' />
            </div>
          </div>
          <div className={stepClass}>
            <div className='step-box'>
              <div className='step-logo'>
                <img src={step} />
              </div>
              <div className='step-title'>
                Intro to Powers
          </div>
              <div className='step-text'>
              Power Perpetuals (Powers) is a novel financial derivative. Similar to a perpetual futures, the perpetual of BTC^2 tracks the index of BTC² rather than BTC.
          </div>
            </div>
            <div className='step-box'>
              <div className='step-logo'>
                <img src={step1} />
              </div>
              <div className='step-title'>
                Unique Payout
          </div>
              <div className='step-text'>
              Power perpetual of BTC^2 provides global options-like payout. For a long position, it makes more when BTC goes up and loses less when BTC goes down. In return, long position holders pay a funding fee for this advantage.
          </div>
            </div>
            <div className='step-box'>
              <div className='step-logo'>
                <img src={step2} />
              </div>
              <div className='step-title'>
                Ola! mBTC^2
              </div>
              <div className='step-text'>
                BTC^2 is a huge number, Deri downscales it to a smaller unit - milli (prefix for 1/1000). Namely, the object being traded on Deri is mBTC^2 and mETH^2, which correspond to 1/1000 of BTC^2 and ETH^2.
              </div>
            </div>
          </div>
          <div className='step-check'>
            <div className={index === 1 ? 'step-no now' : 'step-no'} onClick={() => setStep(1)} ></div>
            <div className={index === 2 ? 'step-no now' : 'step-no'} onClick={() => setStep(2)}></div>
            <div className={index === 3 ? 'step-no now' : 'step-no'} onClick={() => setStep(3)}></div>
          </div>
          <div className='learn-next'>
            <div className='learn-text'>
              <a target="_blank" rel='noreferror' href='https://deri-protocol.medium.com/introducing-power-perpetuals-2b052f655224'>Learn More about Powers</a>
            </div>
            {index !== 3 && <div className='next-button' onClick={nextClick}>
              NEXT
          </div>}
            {index === 3 && <div className='next-button' onClick={closeIntro}>
              OK
          </div>}
          </div>
        </div>
      </div>}
      <div className='open-show' onClick={openShow}>
        <img src={open} alt='?' />
        <span className='show-text'>
          Intro to Powers
        </span>
      </div>
    </>



  )
}