import { Redirect } from "react-router-dom";
import { inject } from "mobx-react";
import { observer } from "mobx-react-lite";
import {useEffect} from 'react'


function IndexRoute({wallet}){
  const location = 
  useEffect(() => {
    if(wallet.detail){
      if(wallet.supportOptions){
        return <Redirect to='/trade/options'/>
      } else {
        return <Redirect to='/trade/futures'/>
      }
    }
  }, [wallet.detail,wallet.supportOptions]);

  const url = window.location.href;
  if(/^https?:\/\/info/.test(url)) {
    return <Redirect to='/info'/>
  }
  return <Redirect to='/trade/options'/>
}

export default inject('wallet')(observer(IndexRoute))