import { request, gql } from 'graphql-request'
import { catchApiError } from '../../shared/utils';

export const getProposals = async () => {
  return catchApiError(async () => {
    const graphqlBase = "https://hub.snapshot.org/graphql";
    const query = `
query Proposals {
  proposals(
    first: 20,
    skip: 0,
    where: {
      space_in: ["deri.eth"],
      state: "active"
    },
    orderBy: "created",
    orderDirection: desc
  ) {
    id
    title
    body
    choices
    start
    end
    snapshot
    state
    author
    space {
      id
      name
    }
  }
}
`;
    const res = await request(graphqlBase, query);
    return { proposals: res.proposals, count: res.proposals.length };
  });
};