import {useCallback, useEffect,useRef,useState} from 'react'
import styled from 'styled-components'
import langIcon from '../../assets/img/lang-icon.svg'
import Tooltip from '../Tooltip/Tooltip'
import languages from '../../locales/lang.json'
import classNames from 'classnames'
import { inject, observer } from 'mobx-react'
import ReactTooltip from 'react-tooltip'
import { addParam, hasParam, getParam } from "../../utils/utils";
const Wrapper = styled.div`
  display : flex;
  align-items: center;
  img {
    cursor : pointer;
  }
`

const Panel = styled.div`
  color : #93A1B8;
  .item:hover {
    // border: 1px solid #CD7A37;
  }
  .item:hover,.item.selected {
    border-radius: 4px;
    background: #5577FD1A;
    color : #E0ECFF;
    cursor : pointer;
  }
  .__react_component_tooltip.show{
    width : 180px;
  }
  .item {
    border : 1px solid transparent;
    padding : 16px;
    height: 48px;
  }
`

function LangSelector({ intl }) {
  const toolTipRef = useRef();
  const [isTooltipOpen, setTooltipOpen] = useState(false)

  const calculatePosition = (position, currentEvent, currentTarget, refNode, place, desiredPlace, effect, offset) => {
    const rect = currentTarget.getBoundingClientRect();
    const top = rect.top + rect.height + 8;
    const left = (rect.left - refNode.offsetWidth + rect.width)
    return { top: top + offset.top, left: left }
  }

  const setClassOnHtmlRoot = () => {
    let locale = document.getElementsByTagName('html')[0]
    locale.lang = intl.locale
  }

  const onClick = (lang, refresh) => {
    intl.setLocale(lang)
    // let url = addParam('locale',lang)
    // window.history.pushState({}, 0, url);
    setClassOnHtmlRoot()
    toolTipRef.current.tooltipRef = null;
    ReactTooltip.hide()
  }

  useEffect(() => {
    if(hasParam('locale') && intl.supportLang(getParam('locale'))){
      onClick(getParam('locale'))
    } else {
      onClick(intl.locale)
    }
    return () => {}
  }, [intl])

  useEffect(() => {
    setClassOnHtmlRoot()
  },[intl.locale])

  return (
    <Wrapper className='lang-select'>
      <img src={langIcon} alt='lang Selector'  data-tip data-for='lang-selector'  data-event='click'  />
      <Tooltip width='150' ref={toolTipRef} isCapture clickable eventOff={null} offset={{ top: 16 }} id='lang-selector' place='bottom' overridePosition={calculatePosition}
        resizeHide={false} className='lang-select-tooltip' type="info" globalEventOff='click' backgroundColor='#203B60'>
        <Panel>
          {Object.keys(languages).map((lang, index) => <div key={index} className={classNames('item', { selected: lang === intl.locale })} onClick={(e) => onClick(lang, true)}>{languages[lang]}</div>)}
        </Panel>
      </Tooltip>
    </Wrapper>
  )
}
export default inject('intl')(observer(LangSelector))