import React, { useState ,useEffect, useCallback} from 'react'
import './walletModal.less'
import Icon from '../Icon/Icon'
import { hide } from 'react-functional-modal'
import Dialog from '../Dialog/Dialog'
import { SUPPORTED_WALLETS } from '../../utils/Constants'
import Spinner from '../Spinner/Spinner'
import { useWallet } from 'use-wallet'
import config from '../../page-network-config.json'
import {useLocation} from 'react-router-dom'
import Wallet from '../../model/Wallet';
import { getDefaultNw } from '../../utils/utils';
import { DeriEnv } from '../../lib/web3js';



export default function WalletModal({walletContext,lang,closeModal,isWrongNetwork,wallet}){
  // const walletContext = useWallet();
  const footer = (
    <div className='wallet-modal-footer'>
      <a href='https://metamask.io/faqs'rel='noreferrer' target='_blank'>{lang['learn-more-about-wallet']}</a>
    </div>)
  return(
    <Dialog title={lang['connect-wallet']} footer={footer} className='wallet-modal' onClose={closeModal}>
      <div className='wallet-modal-body'>
        {Object.keys(SUPPORTED_WALLETS).map((key,index) => (
          <Option walletContext = {walletContext} isWrongNetwork={isWrongNetwork} wallet={wallet} connected = {walletContext.status === 'connected' && walletContext.connector === SUPPORTED_WALLETS[key].connector} walletContext={walletContext} closeModal={closeModal} name={SUPPORTED_WALLETS[key].name} connector={SUPPORTED_WALLETS[key].connector}  key={index} icon={SUPPORTED_WALLETS[key].icon}/>
        ))}
      </div>
    </Dialog>
  )
}

const Option = ({walletContext,connector,name,icon,index,connected,closeModal,isWrongNetwork,wallet}) => {
  const [connecting, setConnecting] = useState(false)
  const chainList = config[DeriEnv.get()];

  const connect = useCallback(async (key) => {
    setConnecting(true)
    await walletContext.connect(key)
    closeModal();
  },[])
   
  useEffect(async () => {
    if(walletContext.status === 'connected') {
      // const pathname = location.pathname;
      // const page = Object.keys(chainList).find(key => pathname.match(new RegExp(`^${key}`)))
      // const supportedChains = chainList[page]
      if(isWrongNetwork()) {
        const defaultNetwork = getDefaultNw(DeriEnv.get());
        await wallet.switchNetwork(defaultNetwork,() => {
          closeModal();
        },async () => {
          const ethereum = window.ethereum
          await walletContext.reset()
          window.ethereum = ethereum
        });
      } 
      setConnecting(false)
    }
    if(walletContext.status === 'error'){
      setConnecting(false)
    }
  }, [walletContext.status])
  return(
    <div className='wallet-modal-body-item' key={index} onClick={() => connect(connector)}>
      <div className='name'>
        <div className='status'>{connected && <div className='connected'></div>}</div> 
        {name}
        {connecting && <Spinner width='14' height='14'/>}
      </div>
      <Icon token={icon} width='38' height='38'/>
    </div>
  )
}