import { checkAddress, checkApiInput, checkChainId, getMiningVaultConfig } from "../config";
import { AppError } from "../error/error";
import { databaseFactory, miningVaultPoolFactory } from "../factory";
import { catchApiError, catchTxApiError, deriToBool, deriToNatural, deriToString, naturalToDeri } from "../utils";
import { getPoolName } from "./shared";

export const getUserInfo = async (chainId, poolAddress, userAddress) => {
    let poolName = getPoolName(chainId, poolAddress)
  const db = databaseFactory(true);
  let values = await db.getValues([
    `dm2.${poolName}.${userAddress}.claim.chainId`,
    `dm2.${poolName}.${userAddress}.claim.amount`,
    `dm2.${poolName}.${userAddress}.claim.deadline`,
    `dm2.${poolName}.${userAddress}.claim.nonce`,
    `dm2.${poolName}.${userAddress}.claim.v`,
    `dm2.${poolName}.${userAddress}.claim.r`,
    `dm2.${poolName}.${userAddress}.claim.s`,
    `dm2.${poolName}.${userAddress}.claim.valid`,
    `dm2.${poolName}.${userAddress}.claim.harvest.lp`,
    `dm2.${userAddress}.claim.total`
  ]);
  // console.log('values', values)
  if (
    Math.floor(new Date().getTime() / 1000) < parseInt(deriToString(values[2]))
  ) {
  } else {
    values = [...values]
    values[7] = false;
  }
  // const [chainId, amount, deadline, nonce, v, r, s, valid] = values;
  const res = {
    chainId: deriToString(values[0]),
    amount: deriToNatural(values[1]).toString(),
    deadline: deriToString(values[2]),
    nonce: deriToString(values[3]),
    v: deriToString(values[4]),
    r: values[5],
    s: values[6],
    valid: deriToBool(values[7]),
    lp: deriToNatural(values[8]).toString(),
    total: deriToNatural(values[9]).toString()
  };
  return res
}

// for lp claim use
export const claimReward = async (chainId, poolAddress, accountAddress, opts) => {
  return catchTxApiError(async () => {
    chainId = checkChainId(chainId)
    poolAddress = checkAddress(poolAddress)
    accountAddress = checkAddress(accountAddress)
    const  res = await getUserInfo(chainId, poolAddress, accountAddress)
    const { deadline, nonce, v, r, s } = res;
    if (res.valid) {
      const miningVaultAddress = getMiningVaultConfig(chainId);
      const miningVault = miningVaultPoolFactory(chainId, miningVaultAddress);
      return await miningVault.mintDToken(
        accountAddress,
        naturalToDeri(res.amount),
        deadline,
        nonce,
        v,
        r,
        s,
        opts
      );
    } else {
      throw new AppError('INVALID_DATA', res);
    }
  }, [chainId, opts]);
};

export const getPendingReward = async (
  chainId,
  poolAddress,
  accountAddress,
) => {
  return catchApiError(
    async () => {
      [chainId, poolAddress, accountAddress] = checkApiInput(
        chainId,
        poolAddress,
        accountAddress,
      );
      return await getUserInfo(chainId, poolAddress, accountAddress)
    },
    [],
    {}
  );
}