export const getUserStakeInfo = async (chainId, poolAddress, accountAddress) => {
  const versionId = getPoolVersionId(poolAddress);
  if (versionId.failed) {
    return { success: false, response: versionId.response };
  }
  if (versionId === 'v3') {
      return await api('v3').getUserStakeInfo(chainId, poolAddress, accountAddress)
  } else {
    //if (DeriEnv.get() === Env.PROD) {
    const res = await fetchJson(
      `${getHttpBase()}/is_user_staked/${chainId}/${poolAddress}/${accountAddress}`
    );
    if (res && res.success) {
      const data = res.data;
      return {
        poolAddress: data.pool,
        accountAddress: data.account,
        isStaked: data.is_staked,
        bTokens: data.btokens.map((b) => {
          if (b.btoken_id) {
            return {
              bTokenId: b.btoken_id,
              isStaked: b.is_staked,
            };
          } else
            return {
              isStaked: b.is_staked,
            };
        }),
      };
    } else {
      return {
        poolAddress: poolAddress,
        accountAddress: accountAddress,
        isStaked: false,
        error: `cannot find the pool config of '${poolAddress}'`,
      };
    }
  }
  // } else {
  //   const versionId = getPoolVersionId(poolAddress);
  //   if (versionId.failed) {
  //     // slp and clp
  //     const configs = await getLpConfigList();
  //     const config = configs.find((c) => c.pool === poolAddress);
  //     if (config) {
  //       return getLpUserStakeInfo(chainId, poolAddress, accountAddress);
  //     } else {
  //       return { success: false, response: versionId.response };
  //     }
  //   }
  //   if (api(versionId).getUserStakeInfo) {
  //     return api(versionId).getUserStakeInfo(
  //       chainId,
  //       poolAddress,
  //       accountAddress
  //     );
  //   } else {
  //     return defaultReturnValue();
  //   }
  // }
};

export const getLiquidityInfo = async (
  chainId,
  poolAddress,
  accountAddress,
  bTokenId
) => {
  const versionId = getPoolVersionId(poolAddress);
  if (versionId.failed) {
    return { success: false, response: versionId.response };
  }
  return api(versionId).getLiquidityInfo(
    chainId,
    poolAddress,
    accountAddress,
    bTokenId
  );
};

export const getEstimatedLiquidityInfo = async (
  chainId,
  poolAddress,
  accountAddress,
  newAmount,
  bTokenId,
) => {
  const versionId = getPoolVersionId(poolAddress);
  if (versionId.failed) {
    const lpPools = await getLpConfigList(DeriEnv.get())
    if (lpPools.map((p) => p.pool).includes(poolAddress)) {
      // hotfix to clp/slp pools
      const res = await getLpLiquidityInfo(chainId, poolAddress, accountAddress)
      if (res.success) {
        const { shares, poolLiquidity } = res.response.data
        return {
          percentage1: bg(shares)
            .plus(newAmount)
            .div(bg(poolLiquidity).plus(bg(newAmount)))
            .toString(),
          shares1: bg(shares).plus(newAmount).toString(),
        };
      }
    }
    return { success: false, response: versionId.response };
  }
  return api(versionId).getEstimatedLiquidityInfo(
    chainId,
    poolAddress,
    accountAddress,
    newAmount,
    bTokenId,
  );
};

export const getPoolLiquidity = async (chainId, poolAddress) => {
  return catchApiError(async () => {
    chainId = checkChainId(chainId);
    poolAddress = checkAddress(poolAddress);
    let result = await getPoolConfigList(DeriEnv.get());
    let configs = [];
    if (result.success) {
      configs = result.response.data;
    }
    // add lp config
    configs = configs.concat(await getLpConfigList(DeriEnv.get()));
    const config = configs.find((v) => v.pool === poolAddress);
    if (!config) {
      throw new AppError('CONFIG_NOT_FOUND', {
        name: 'getPoolConfig',
        args: [DeriEnv.get(), poolAddress],
      });
    }
    const db = databaseFactory();
    let res;
    // v2
    if (config.bTokens.length > 1 && config.type !== 'lp') {
      const bTokenIds = config.bTokens.map((b) => b.bTokenId);
      const key = bTokenIds.reduce(
        (acc, bTokenId) => [
          ...acc,
          `${chainId}.${poolAddress}.liquidity${bTokenId}`,
        ],
        [`${chainId}.${poolAddress}.liquidity`]
      );
      const result = await db.getValues(key);
      const totalValue = fromWei(result[0]).toString();
      const bTokens = bTokenIds.reduce((acc, bTokenId, index) => {
        return [
          ...acc,
          {
            bTokenId,
            liquidity: fromWei(result[1 + index]).toString(),
          },
        ];
      }, []);
      res = {
        totalValue,
        bTokens,
      };
    } else {
    // v2 lite, option or lp
      const result = await db.getValues([
        `${chainId}.${poolAddress}.liquidity`,
      ]);
      const liquidity = fromWei(result[0]).toString();
      res = {
        totalValue: liquidity,
        bTokens: [{ liquidity }],
      };
    }
    return res;
  });
};


export const getPoolInfoApy = async (chainId, poolAddress) => {
  return catchApiError(async () => {
    chainId = checkChainId(chainId);
    poolAddress = checkAddress(poolAddress);
    let result = await getPoolConfigList(DeriEnv.get());
    let configs = [];
    if (result.success) {
      configs = result.response.data;
    }
    // add lp config
    configs = configs.concat(await getLpConfigList(DeriEnv.get()));
    const config = configs.find((v) => v.pool === poolAddress);
    if (!config) {
      throw new AppError('CONFIG_NOT_FOUND', {
        name: 'getPoolConfig',
        args: [DeriEnv.get(), poolAddress],
      });
    }
    const db = databaseFactory(true);
    const dbTest = databaseFactory();
    let res;
    if (config.bTokens.length > 1 && config.type !== 'lp') {
      if (config.versionId === 'v3') {
        let networkName = fixGetNetworkName(chainId);
        const key = config.bTokens.reduce(
          (acc, bToken) => [
            ...acc,
            ...[
              `${networkName}.${poolAddress}.apy.${bToken.bTokenSymbol.toUpperCase()}`,
              `${networkName}.${poolAddress}.multiplier.${bToken.bTokenSymbol.toUpperCase()}`,
            ],
          ],
          []
        );
        const apyKey = [`reward.${chainId}.${poolAddress}.apy`]
        const [result, deriApy] = await Promise.all([
          db.getValues(key),
          dbTest.getValues(apyKey),
        ])
        const deriApys = config.bTokens.map((b, index) => ({
          //deriApy: fromWei(result[index * 2]),
          // deriApy,
          deriApy: fromWei(deriApy[0]),
          multiplier: fromWei(result[index * 2 + 1]),
        }));
        const vApys = await getBTokenApyAndDiscounts(
          chainId,
          poolAddress,
          config.bTokens,
        );
        const apyArgs = deriApys.map((a, index) =>
          bg(a.deriApy)
            .plus(bg(vApys[index].supplyApy).div(100))
            .plus(bg(vApys[index].xvsApy).div(100))
        ).map((a) => a.toNumber());
        const maxApy = Math.max(...apyArgs).toString();
        //console.log(maxApy.toString())
        const bTokens = config.bTokens.reduce((acc, bToken, index) => {
          return [
            ...acc,
            {
              bTokenSymbol: bToken.bTokenSymbol,
              apy: deriApys[index].deriApy,
              // multiplier: deriApys[index].multiplier,
              multiplier: bg(vApys[index].factor).times(1.25).toString(),
              supplyApy: bg(vApys[index].supplyApy).div(100).toString(),
              xvsApy: bg(vApys[index].xvsApy).div(100).toString(),
            },
          ];
        }, []);
        res = {
          maxApy,
          bTokens,
        };
      } else {
        // v2
        const bTokenIds = config.bTokens.map((b) => b.bTokenId);
        let networkName = fixGetNetworkName(chainId);
        // console.log('config', config)
        const key = bTokenIds.reduce(
          (acc, bTokenId) => [
            ...acc,
            ...[
              `${networkName}.${poolAddress}.apy${bTokenId}`,
              `${networkName}.${poolAddress}.V2.multiplier${bTokenId}`,
            ],
          ],
          []
        );
        const result = await db.getValues(key);
        const apys = result.reduce(
          (acc, a, index) => [
            ...acc,
            ...(index % 2 === 0 ? [fromWei(a)] : []),
          ],
          []
        ).map((a) => bg(a).toNumber());
        const maxApy = Math.max(...apys).toString();
        const bTokens = bTokenIds.reduce((acc, bTokenId, index) => {
          return [
            ...acc,
            {
              bTokenId,
              apy: fromWei(result[index * 2]).toString(),
              multiplier: fromWei(result[index * 2 + 1]).toString(),
            },
          ];
        }, []);
        res = {
          maxApy,
          bTokens,
        };
      }
    } else if (config.type === 'lp') {
      const deriAddress = (await getDeriConfig(chainId)).deriAddress
      const result = await db.getValues([
        `dm2.${fixGetNetworkName(chainId)}.${poolAddress}.apy`,
        `dm2.${fixGetNetworkName(chainId)}.${poolAddress}.apy2`,
        `dm2.${fixGetNetworkName(chainId)}.${deriAddress}.deri.price`,
      ]);
      const [apy1, apy2] = [
        fromWei(result[0]).toString(),
        fromWei(result[1]).toString(),
      ];
      res = {
        maxApy: max(bg(apy1), bg(apy2)).toString(),
        bTokens: [{ apy: apy1 }, { apy: apy2 }],
      };
    } else {
      // single base token
      const result = await db.getValues([
        config.chain.startsWith('bsc') && config.bTokens[0].bTokenSymbol === 'DERI'
          ? `dm2.${fixGetNetworkName(chainId)}.${poolAddress}.apy`
          : `${fixGetNetworkName(chainId)}.${poolAddress}.apy`,
      ]);
      const apy = fromWei(result[0]).toString();
      res = {
        maxApy: apy,
        bTokens: [{ apy, multiplier: '1' }],
      };
    }
    return res;
  });
};

export const getPoolTradersApy = async (chainId, poolAddress) => {
  return catchApiError(async () => {
    chainId = checkChainId(chainId);
    poolAddress = checkAddress(poolAddress);
    let result = await getPoolConfigList(DeriEnv.get());
    let configs = [];
    if (result.success) {
      configs = result.response.data;
    }
    const config = configs.find((v) => v.pool === poolAddress);
    if (!config) {
      throw new AppError('CONFIG_NOT_FOUND', {
        name: 'getPoolConfig',
        args: [DeriEnv.get(), poolAddress],
      });
    }
    if (config.bTokens.length > 1 && config.versionId === 'v3') {
      // const vApys = await Promise.all(
      //   config.bTokens.reduce(
      //     (acc, b) => [...acc, getBTokenApyAndDiscount(b.bTokenSymbol)],
      //     []
      //   )
      // );

      const vApys = await getBTokenApyAndDiscounts(
        chainId,
        poolAddress,
        config.bTokens,
      );
      const bTokens = config.bTokens.reduce((acc, bToken, index) => {
        return [
          ...acc,
          {
            bTokenSymbol: bToken.bTokenSymbol,
            supplyApy: vApys[index].supplyApy,
            xvsApy: vApys[index].xvsApy,
          },
        ];
      }, []);
      return bTokens;
    }
    return [];
  });
};

export const getPendingReward = async (
  chainId,
  poolAddress,
  accountAddress,
) => {
  let result = await getPoolConfigList(DeriEnv.get());
  let configs = [];
  if (result.success) {
    configs = result.response.data;
  }
  // add lp config
  configs = configs.concat(await getLpConfigList(DeriEnv.get()));
  const config = configs.find((v) => v.pool === poolAddress);
  let res
  if (config && ['v3', 'v3_lite', 'v2_lite', 'slp', 'clp'].includes(config.version)) {
    if (['v3', 'v3_lite'].includes(config.version)) {
      res = await getPendingRewardV3(chainId, poolAddress, accountAddress)
    } else {
      res = await getPendingRewardV2Lite(chainId, poolAddress, accountAddress)
    }
  } else {
    res = {
      success: false,
      response: {
        data: '',
        error: { message: 'The pool version is invalid and should in [v3, v2_lite, slp and clp]' },
      },
    };
  }
  return res
};