import Icon from "../Icon/Icon";
import './dialog.less'
import classNames from "classnames";

export default function Dialog({title,className,onClose,body,footer,children}){
  return(
    <div className={classNames('dialog',className)}>
      <div className='dialog-header'>
        {title} <Icon token='close-modal-icon' className='close-wallet-modal' onClick={onClose}/>
      </div>
      <div className='dialog-body'>
        {children ||body }
      </div>
      {footer && <div className='dialog-footer'>
        {footer}
      </div>}
    </div>
  )
}