import { checkApiInput } from '../../shared/config';
import { catchTxApiError } from '../../shared/utils';
import { everlastingOptionFactory } from '../factory/pool.js';

export const addLiquidity = async(chainId, poolAddress, accountAddress, amount, opts) => {
  // const args = [chainId, poolAddress, accountAddress, amount]
  return catchTxApiError(async () => {
    [chainId, poolAddress, accountAddress ] = checkApiInput(
      chainId,
      poolAddress,
      accountAddress
    );
    const optionPool = everlastingOptionFactory(chainId, poolAddress)
    return await optionPool.addLiquidity(accountAddress, amount, opts)
  }, [chainId, opts])
}

export const removeLiquidity = async(chainId, poolAddress, accountAddress, amount, isMaximum=false, opts) => {
  return catchTxApiError(async () => {
    [chainId, poolAddress, accountAddress ] = checkApiInput(
      chainId,
      poolAddress,
      accountAddress
    );
    const optionPool = everlastingOptionFactory(chainId, poolAddress)
    return await optionPool.removeLiquidity(accountAddress, amount, isMaximum, opts)
  }, [chainId, opts])
}
