import Web3 from 'web3'
import { getPoolVersionId } from '../shared/config/pool';
import { unlockDeri } from '../shared/api';
import { api } from './api_shared'

export const unlock = async (
  chainId,
  poolAddress,
  accountAddress,
  bTokenId,
  opts,
) => {
  const versionId = getPoolVersionId(poolAddress);
  if (versionId.failed) {
    // for deri
    return unlockDeri(chainId, poolAddress);
  }
  return api(versionId).unlock(chainId, poolAddress, accountAddress, bTokenId, opts)
};

export const depositMargin = async (
  chainId,
  poolAddress,
  accountAddress,
  amount,
  bTokenId,
  opts,
) => {
  const versionId = getPoolVersionId(poolAddress);
  if (versionId.failed) {
    return { success: false, response: versionId.response };
  }
  // fix v2_lite bTokenId undefined issue
  if (bTokenId == null && versionId.startsWith('v2_lite')) {
    bTokenId = opts
  }
  return api(versionId).depositMargin(
    chainId,
    poolAddress,
    accountAddress,
    amount,
    bTokenId,
    opts,
  );
};

export const withdrawMargin = async (
  chainId,
  poolAddress,
  accountAddress,
  amount,
  bTokenId,
  isMaximum,
  opts,
) => {
  if (opts == null && typeof isMaximum === 'object') {
    opts = isMaximum
    isMaximum = false
  }
  const versionId = getPoolVersionId(poolAddress);
  if (versionId.failed) {
    return { success: false, response: versionId.response };
  }
  // fix v2_lite bTokenId undefined issue
  if (bTokenId == null && versionId.startsWith('v2_lite')) {
    bTokenId = opts
  }
  return api(versionId).withdrawMargin(
    chainId,
    poolAddress,
    accountAddress,
    amount,
    bTokenId, 
    isMaximum,
    opts,
  );
};

