
import { bg } from "../../../../utils/utils"
import { checkAddress, checkChainId, DEBUG } from "../../shared/config"
import { catchApiError, catchTxApiError } from "../../shared/utils"
import { getVoteConfig } from "../config"
import { voteFactory } from "../contract/factory/rest"
import { Vote } from "../contract/gen/Vote"

const _getUserVotingPower = async (accountAddress) => {
  accountAddress = checkAddress(accountAddress)
  const votes = (['1', '56', '42161']).map((chainId) => {
    const voteConfig = getVoteConfig(chainId)
    return voteFactory(voteConfig.chainId, voteConfig.address, { useProvider: true})
  })
  const [voteETH, voteBNB, voteARBI] = votes
  const powers = await Promise.all([
    voteETH.getVotePowerOnEthereum(accountAddress),
    voteBNB.getVotePowerOnBNB(accountAddress),
    voteARBI.getVotePowerOnArbitrum(accountAddress),
  ])
  return powers.reduce((acc, power) => bg(acc).plus(power), bg(0)).toString()
}

export const getUserVotingPower = async (accountAddress) => {
  return catchApiError(_getUserVotingPower, [accountAddress], '')
}

export const getUserVotingResult = async (accountAddress) => {
  return catchApiError(async () => {
    accountAddress = checkAddress(accountAddress)
    const votes = (['1', '56', '42161']).map((chainId) => {
      const voteConfig = getVoteConfig(chainId)
      return voteFactory(voteConfig.chainId, voteConfig.address, { useProvider: true})
    })
    const [voteETH, voteBNB, voteARBI] = votes
    const powers = await Promise.all([
      voteETH.getVotePowerOnEthereum(accountAddress),
      voteBNB.getVotePowerOnBNB(accountAddress),
      voteARBI.getVotePowerOnArbitrum(accountAddress),
    ])
    // get voters in each chain
    const allChainVoters = await Promise.all([
      voteETH.getVoters(),
      voteBNB.getVoters(),
      voteARBI.getVoters(),
    ])
    const [[optionETH], [optionBNB], [optionARBI]]= await Promise.all([
      voteETH.getVotes([accountAddress]),
      voteBNB.getVotes([accountAddress]),
      voteARBI.getVotes([accountAddress]),
    ])
    // get voters in all chain
    let voters = allChainVoters.reduce((acc, voter) => acc.concat(voter), [])
    voters = [...new Set(voters)]
    const power = powers.reduce((acc, power) => bg(acc).plus(power), bg(0)).toString()
    let option = '0'

    // check voter is in voters
    // console.log(`---`, voters, allChainVoters[0])
    if (voters.includes(accountAddress)) {
      // option = (allChainVoters[0].includes(accountAddress) && bg(optionETH).gt(0))
      //   ? optionETH : (allChainVoters[1].includes(accountAddress) && bg(optionBNB).gt(0))
      //     ? optionBNB : optionARBI
      option = [optionETH, optionBNB, optionARBI].reduce((acc, op, index) => {
        if (allChainVoters[index].includes(accountAddress) && bg(op).gt(0)) {
          acc = op
        }
        return acc
      }, '0')
    }
    return {
      account: accountAddress,
      votingPower: power,
      option,
    }
  })
}

// helper
const _getVotingInfo = async (vote) => await Promise.all([
  vote.topic(),
  vote.deadline(),
  vote.numOptions(),
])

export const getVotingInfo = async () => {
  return catchApiError(async () => {
    const votes = (['1', '56', '42161']).map((chainId) => {
      const voteConfig = getVoteConfig(chainId)
      return voteFactory(voteConfig.chainId, voteConfig.address, { useProvider: true })
    })
    const votesinfo = await Promise.all(votes.map((vote) => _getVotingInfo(vote)))
    return votesinfo.map((voteInfo, index) => ({
      chainId: votes[index].chainId,
      topic: voteInfo[0],
      deadline: voteInfo[1],
      numOptions: voteInfo[2],
    }))
  })
}

export const getVotingResult = async () => {
  return catchApiError(async() => {
    const votes = (['1', '56', '42161']).map((chainId) => {
      const voteConfig = getVoteConfig(chainId)
      return voteFactory(voteConfig.chainId, voteConfig.address, { useProvider: true})
    })
    const [voteETH, voteBNB, voteARBI] = votes
    const [topic, deadline, numOptions] = await Promise.all([
      voteBNB.topic(),
      voteBNB.deadline(),
      voteBNB.numOptions(),
    ])
    const options = (new Array(parseInt(numOptions))).fill(0).map((_, index) => (index + 1).toString())
    const allChainVoters = await Promise.all([
      voteETH.getVoters(),
      voteBNB.getVoters(),
      voteARBI.getVoters(),
    ])
    let voters = allChainVoters.reduce((acc, voter) => acc.concat(voter), [])
    voters = [...new Set(voters)]
    DEBUG && console.log(`--- voters:`, voters)
    const voteOptions = await Promise.all([
      voteETH.getVotes(voters),
      voteBNB.getVotes(voters),
      voteARBI.getVotes(voters),
    ])
    DEBUG && console.log(`--- options:`, voteOptions)
    const powers = await Promise.all([
      voteETH.getVotePowersOnEthereum(voters),
      voteBNB.getVotePowersOnBNB(voters),
      voteARBI.getVotePowersOnArbitrum(voters),
    ])
    DEBUG && console.log(`--- powers:`, powers)
    let optionPowers = (new Array(parseInt(numOptions))).fill(0).map(() => [])
    for (let i = 0; i < voters.length; i++) {
      const voter = voters[i]
      // get user options in different chain
      const userOptions = voteOptions.reduce((acc, o) => acc.concat([o[i]]), [])
      // get user option
      const opt = parseInt(userOptions.find((option, index) => allChainVoters[index].includes(voter) && bg(option).gt(0)) || '0')
      // console.log(voter, 'option', opt, numOptions)
      if (opt <= 0 && opt > parseInt(numOptions)) {
        console.log(`invalid option ${opt} for the user ${voter}`)
      } else {
        optionPowers[opt - 1].push(powers.reduce((acc, power) => bg(acc).plus(power[i]), bg(0)).toString())
      }
    }
    let res = optionPowers.reduce((acc, option, index) => {
      acc[`option_${index + 1}`] = option.reduce((acc, i) => bg(acc).plus(i), bg(0)).toString()
      return acc
    }, {})
    // res.topic = topic
    // res.deadline = deadline
    // res.numOptions = parseInt(numOptions)
    return res
  })
}

export const vote = async (chainId, accountAddress, votingOption, opts) => {
  return catchTxApiError(async () => {
    chainId = checkChainId(chainId)
    accountAddress = checkAddress(accountAddress);
    const voteConfig = getVoteConfig(chainId)
    const vote = new Vote(voteConfig.chainId, voteConfig.address)
    const numOptions = await vote.numOptions();
    if (bg(votingOption).gt(numOptions) || bg(votingOption).lte(0)) {
      throw new Error(
        `Deri Vote: invalid votingOption(0 < votingOption <=${numOptions}): ${votingOption}`
      );
    }
    return await vote.vote(accountAddress, votingOption, opts);
  }, [chainId, opts]);
}